<template>
    <div id="edit-admin">
        <h3>Edit admin</h3>
        <div class="row">
            <form @submit.prevent="updateAdmin" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input disabled type="text" v-model="admin_id" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="first_name" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="last_name" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="department" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="position" required>
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
    import db from './firebaseInit'
    export default {
        name: 'edit-admin',
        data () {
            return {
                admin_id: null,
                first_name: null,
                last_name: null,
                department: null,
                position: null
            }
        },
        beforeRouteEnter(to, from, next) {
            db.collection('Administrator').where('admin_id',
                '==', to.params.admin_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    next(vm => {
                        vm.admin_id = doc.data().admin_id
                        vm.first_name = doc.data().first_name
                        vm.last_name = doc.data().last_name
                        vm.department = doc.data().department
                        vm.position = doc.data().position
                    })
                })
            })
        },
        watch: {
            '$route': 'fetchData'
        },
        methods: {
            fetchData() {
                db.collection('Administrator').where('admin_id',
                    '==', this.$route.params.admin_id).get().then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        this.admin_id = doc.data().admin_id
                        this.first_name = doc.data().first_name
                        this.last_name = doc.data().last_name
                        this.department = doc.data().department
                        this.position = doc.data().position
                    })
                })
            },
            updateAdmin () {
                db.collection('Administrator').where('admin_id',
                    '==', this.$route.params.admin_id).get().then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.update({
                            admin_id: this.admin_id,
                            first_name: this.first_name,
                            last_name: this.last_name,
                            department: this.department,
                            position: this.position
                        }).then(() => {
                            this.$router.push({name: 'view-admin', params: {admin_id: this.admin_id}})
                        })
                    })
                })
            }
        }
    }
</script>