<template>
    <div id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header"><h4>Admins</h4></li>
            <li v-for="admins in Administrator" v-bind:key="admins.id" class="collection-item">
                <div class="chip">{{admins.department}}</div>
                {{admins.admin_id}}:{{admins.first_name}} {{admins.last_name}} - Status: {{admins.position}}

            <router-link class="secondary-content" v-bind:to="{name: 'view-admin', params: {admin_id: admins.admin_id}}">
                <i class="fa fa-eye"></i>
            </router-link>
            </li>
        </ul>

        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large red">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
    import db from './firebaseInit'
    export default {
        name: 'dashboard',
        data () {
            return {
                Administrator: []
            }
        },
       created () {
            db.collection('Administrator').orderBy('first_name').get().then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        const data = {
                            'id': doc.id,
                            'admin_id':doc.data().admin_id,
                            'first_name':doc.data().first_name,
                            'last_name':doc.data().last_name,
                            'department':doc.data().department,
                            'position':doc.data().position
                    }
                    this.Administrator.push(data)
                })
            })
       }
    }
</script>