<template>
    <div id="new-admin">
        <h3>New admin</h3>
        <div class="row">
            <form @submit.prevent="saveAdmin" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="admin_id" required>
                        <label>Admin ID#</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="first_name" required>
                        <label>Firstname</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="last_name" required>
                        <label>Lastname</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="department" required>
                        <label>Department</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="position" required>
                        <label>Position</label>
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
    import db from './firebaseInit'
    export default {
        name: 'new-admin',
        data () {
            return {
                admin_id: null,
                first_name: null,
                last_name: null,
                department: null,
                position: null
            }
        },
        methods: {
            saveAdmin () {
                db.collection('Administrator').add({
                    admin_id: this.admin_id,
                    first_name: this.first_name,
                    last_name: this.last_name,
                    department: this.department,
                    position: this.position
                }).then(this.$router.push('/'))
            }
        }
    }
</script>