<template>
    <div id="view-admin">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>{{first_name }} {{last_name }} </h4>
            </li>
            <li class="collection-item">Admin ID#: {{admin_id}}</li>
            <li class="collection-item">Department: {{department}}</li>
            <li class="collection-item">Postion: {{position}}</li>
        </ul>
        <router-link to="/" class="btn grey">Back</router-link>
        <button @click="deleteAdmin"  class="btn red">Delete</button>
                <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-admin', params: {admin_id: admin_id}}" class="btn-floating btn-large red">
                <i class="fa fa-pencil"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
    import db from './firebaseInit'
    export default {
        name: 'view-admin',
        data() {
            return {
                admin_id: null,
                first_name: null,
                last_name: null,
                department: null,
                position: null
            }
        },
        beforeRouteEnter(to, from, next) {
            db.collection('Administrator').where('admin_id',
                '==', to.params.admin_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    next(vm => {
                        vm.admin_id = doc.data().admin_id
                        vm.first_name = doc.data().first_name
                        vm.last_name = doc.data().last_name
                        vm.department = doc.data().department
                        vm.position = doc.data().position
                    })
                })
            })
        },
        watch: {
            '$route': 'fetchData'
        },
        methods: {
            fetchData() {
                db.collection('Administrator').where('admin_id',
                    '==', this.$route.params.admin_id).get().then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        this.admin_id = doc.data().admin_id
                        this.first_name = doc.data().first_name
                        this.last_name = doc.data().last_name
                        this.department = doc.data().department
                        this.position = doc.data().position
                    })
                })
            },
            deleteAdmin () {
                if(confirm('Are you sure?')) {
                    db.collection('Administrator').where('admin_id',
                    '==', this.$route.params.admin_id).get().then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.delete();
                        this.$router.push('/')
                    })
                })
                }
            }
        }
    }
</script>